



















































import {Component, Vue} from 'vue-property-decorator';

import axios from 'axios';
import App from '../../App.vue';
import RechercheItem from './model/RechercheItem';
import KfwStore, {InfoListener} from '../../KfwStore';

import TextRechercheFeld from '../../components/gui/TextRechercheFeld.vue';
import Alert from '../../components/gui/Alert.vue';
import {RechercheTableHeaders} from './model/FondSettings';
import kfwStore from '../../KfwStore';
import ProkollVorgangUtils from '../../services/ProkollVorgangUtils';
import RatingsService from '../betFonds/service/FondsService';
import ServiceRequestHandler from '../../services/ServiceRequestHandler';




@Component(
  {
    components:{
      TextRechercheFeld,
    }
  }

)
export default class KfwApp extends Vue implements InfoListener,ServiceRequestHandler{
    public static kfwAppInstance:KfwApp;
    public themeMiddle: string = 'app_grad_blue_middle';
    public theme: string = 'app_toolbar_blue';
    public themeDark:boolean = true;

    public search: string = '';
    public connectToDB: boolean = true;
    public partnerName: string = '';
    public partnerId: string = '';

    public resultSearch:string='';
    public serverResp:string = 'TT';
    public searchDisabled:boolean = false;
    public RechercheTableHeaders = RechercheTableHeaders;

    public activeItem!:any;
    private ratingsService:RatingsService = new RatingsService();

    
    constructor() {

        super();
        //console.debug("KFW APP is Constructed for a new instance .....................");
        //console.trace();
        
    }




    public resetFieldsByEmptySearch():void{
      KfwStore.setProperty(KfwStore.PROPERTY_SERACH_TEXT,this.search);
      if (KfwStore.isTextEmpty(this.search)){
          this.resetFields();
      }
    }

    public resetFields():void{
        console.debug("Resetting fields ....");
        this.search = '';
        this.partnerName = '';
        this.partnerId = '';
        KfwStore.setProperty(KfwStore.PROPERTY_RECHERCHE_LISTE,[]);
        KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_NEXT,false);
        KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_BACK,false);
      
    }


  
    mounted():void{
      console.log('Setting the kfw application instance !!');
      console.info(this);
      // For testing
      //KfwStore.showError("Testing the alert....");
      console.log(this.$route);
      KfwStore.addListenerWithName(this,'KfwApp.vue');
      if (KfwApp.kfwAppInstance==undefined) KfwApp.kfwAppInstance = this;
      
    }



    // Sending MOCK requst to Recherche Project
    queryRatings2():void{
      let targetURL : string = KfwStore.fetchURL('RatingAnzeigeRest');
      console.log(' ******************  ENV :: ' + process.env.NODE_ENV);
      if (this.search)
        targetURL += '?QR_VORGANGSOBJEKT=' + this.search;
      console.log("Fetching from URL " + targetURL);
      KfwStore.showDefaultProgressBar();
      axios.get(targetURL).then(res => {
        console.log(res);
        if ((res.data.items)&&(res.data.items.length>0)){
          KfwStore.setProperty(KfwStore.PROPERTY_RATINGS_LISTE,res.data.items);
          this.$router.replace({
            name: 'history',
            query:{
              search : this.search,
            }
          } );
        }else{
          console.error("No Ratings results found !!");
          KfwStore.showNoDataError();
        }
      }).catch(error =>{
        KfwStore.showError(error);
      }).finally(() => {
        KfwStore.hideProgressBar();
      });
      
    }    

  // Sending real  request via a flat GET request  
  queryRatings1(){
    //console.info(this);
    if (this.activeItem) {

      let vorgangsNumber = kfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
      console.log('Reading the ratings for ' + this.activeItem.HGP_ID + " holding the vorgangsnumber " + vorgangsNumber);
      let ratingsURL = KfwStore.fetchExternalURL('Vor','RatingAnzeigeVUE');
      let ratingsURLInfo = `?timestamp=${new Date().getTime()}&format=json&ACTION=Weiter&ACTION_INT=0&AKTUELLE_MASKE=HOBEingabeGP&EINGEBEN=true&GRUPPE_AUSFUEHREN=false&MANDANT=1&ME_BEENDEN=false&POSITIONIEREN=true&TERMINIEREN=false&VORGANGSTYP=0&VORHANDEN=true&WEB_APP_OBJECT=RatingAnzeige&WEB_APP_PATH=RatingAnzeigeVUE&VORGANGSOBJEKT=${this.activeItem.HGP_ID}&fld_DisplayVORGANGSOBJEKT=${this.activeItem.HGP_ID}&fld_GPID=${this.activeItem.GPID}&fld_GPNAME=${this.activeItem.NAME}&fld_MANDANT=1&fld_BEZART=4&fld_VORGANGSOBJEKT=${this.activeItem.HGP_ID}&VORGANGSNUMMER=${vorgangsNumber}`;
      
      ratingsURL += ratingsURLInfo; 
      console.log('Ratings URL : ' + ratingsURL);
      KfwStore.showDefaultProgressBar();
      axios.get(ratingsURL).then(res => {
      if (res.data){
          console.log("Received Response : ");
          console.log(res.data);
          //ToDo the code below should be updated later on with a better error detection handling
          let messages = res.data.PROTOKOLL.BODY.MESSAGES;
          if (messages && messages.MESSAGE){
            kfwStore.populateMessage(messages.MESSAGE);
            this.handleBackAction(true);
          }else{
            let fields = res.data.PROTOKOLL.BODY.FIELDS.FIELD;
            if ((fields!=undefined)  && (fields!=null) && (Object.keys(fields).length>0)){
                let rtgs = fields.find(curFld => curFld.Name=='IntRatings');
                if (rtgs){
                  console.log("Found ratings ");
                  console.log(rtgs);
                  //this.processIncomingRatings(rtgs.RECORDS.RECORD);
                }
              
            }else{
              this.empty();
            }
          }

      }else{
          console.error("No Search results found !!");
          this.empty();
      }
      }).catch(error =>{
          KfwStore.showError(error);
      }).finally(() => {
          KfwStore.hideProgressBar();
      });
    }else{
      //ToDo deactivated code below should be re activated later, after fixing the single Vue Component issue
      //console.error("No selected item is set, can not send ratings request");
    }

  }

  // Sending real request as JSON POST (Semi HAL)
  queryRatings3(){
    //console.info(this);
    if (this.activeItem) {

      let vorgangsNumber = kfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
      console.log('Reading the ratings for ' + this.activeItem.HGP_ID + " holding the vorgangsnumber " + vorgangsNumber);

      let utils :ProkollVorgangUtils  = new ProkollVorgangUtils();
      let flds:any[] = [
        {
          name: 'DisplayVORGANGSOBJEKT',
          content: this.activeItem.HGP_ID
        },
        {
          name: 'MANDANT',
          content: '1'
        },
        {
          name: 'BEZART',
          content: '4'
        },
        {
          name: 'GPID',
          content: this.activeItem.GPID
        },
        {
          name: 'VORGANGSOBJEKT',
          content: this.activeItem.HGP_ID
        },
        {
          name: 'GPNAME',
          content: this.activeItem.NAME
        },

        ];

        KfwStore.showDefaultProgressBar();
        KfwStore.setProperty(KfwStore.PROPERTY_ACTIVE_HGP,this.activeItem);
        axios.post("http://localhost:9080/RatingAnzeigeVUE/GenericKfwRest",utils.createNextRequest(vorgangsNumber,this.activeItem.HGP_ID,flds)).then(res =>{
          if (res.data){
            console.log("Received Response : ");
            console.log(res.data);
            //ToDo the code below should be updated later on with a better error detection handling
            let messages = res.data.PROTOKOLL.BODY.MESSAGES;
            if (messages && messages.MESSAGE){
              KfwStore.populateMessage(messages.MESSAGE);
              this.handleBackAction(true);
            }else{

                let items = utils.getRecords(res.data.PROTOKOLL,"IntRatings");
                if (items){
                  KfwStore.setProperty(KfwStore.PROPERTY_RATINGS_LISTE,items);
                  this.openPath({
                    name: 'history',
                    query:{
                      search : this.search,
                    }
                  });
                }else{
                  this.empty();
                }

          }

      }else{
          console.error("No Search results found !!");
          this.empty();
      }
      }).catch(error =>{
          KfwStore.showError(error);
      }).finally(() => {
          KfwStore.hideProgressBar();
      });
    }else{
      //ToDo deactivated code below should be re activated later, after fixing the single Vue Component issue
      //console.error("No selected item is set, can not send ratings request");
    }


  }
   
  success(){
    this.openPath({
      name: 'history',
      query:{
        search : this.search,
      }
    });
  }

  fail(){
    this.handleBackAction(true);
  }

 


  empty(){
    KfwStore.showError("No Results were received from the server");
    this.handleBackAction(true);
  }

    

    public handleItemSelection(item:any):void {
      console.info("KFw APP had received the selected item " + JSON.stringify(item));
      //console.info(this);
      App.handleVorgangActivation();
      this.activeItem = item;
      this.partnerId = item.GPID;
      this.partnerName = item.NAME;
      this.search = item.HGP_ID;
      
      
    } 

    public stateChanged(property:string,newValue:any):void{
      if (property==KfwStore.PROPERTY_THEME_DARK)
        this.themeDark = newValue;
      if (property==KfwStore.PROPERTY_THEME_CLASS)
        this.theme = newValue;
      if (property==KfwStore.PROPERTY_THEME_MIDDLE)
        this.themeMiddle = newValue;
      if (property==KfwStore.PROPERTY_ACTION_NEXT)
        this.handleNextAction(newValue);
      if (property==KfwStore.PROPERTY_ACTION_BACK)
        this.handleBackAction(newValue);

  }

    handleNextAction(value:boolean):void{
        if (!value) return;
        this.searchDisabled = true;
        this.ratingsService.queryRatingHistory(this.activeItem,this);
    }

    handleBackAction(value:boolean):void{
        if (!value) return;
        this.searchDisabled = false;
        this.openPath('/');
        this.resetFields();

    }

  openPath(path:any):void{ 
    if(this.$router.currentRoute.path!=path)
        this.$router.replace(path);

  }


}






