import Vue from 'vue';

import ActApp from './App.vue';




export interface InfoListener{
    stateChanged(property:string,value:any);
}

export class KfwStore{
    listeners :InfoListener[]=[];
    listenerNames :string[]=[];

    public PROPERTY_CLEAR_LOGIN: string = 'clearLogIn';
    public PROPERTY_THEME_DARK: string = 'themeDark';
    public PROPERTY_THEME_CLASS: string = 'themeClass';
    public PROPERTY_THEME_MIDDLE: string = 'themeMiddle';
    public PROPERTY_SERACH_TEXT: string = 'searchText';
    public PROPERTY_RECHERCHE_LISTE: string = 'rechercheListe';
    public PROPERTY_RATINGS_LISTE: string = 'ratingsListe';
    public PROPERTY_ACTION_NEXT: string = 'actionNext';
    public PROPERTY_ACTION_BACK: string = 'actionBack';

    public PROPERTY_ACTIVATE_NEXT: string = 'activateNext';
    public PROPERTY_ACTIVATE_BACK: string = 'activateBack';

    public PROPERTY_HIDE_RECHERCHE: string = 'hideRecherche';

    public PROPERTY_SHOW_ALERT: string = 'showAlert';
    public PROPERTY_ALERT_TEXT: string = 'alertText';
    

    public PROPERTY_VORGANGS_NUMBER: string = 'vorgangsNumber';
    public PROPERTY_CLEAR_ALERTS: string = 'clearAlerts';
    public PROPERTY_ACTIVE_HGP: string = 'activeHGP';
    public PROPERTY_RATING_VIEW_ITEM: string = 'ratingViewItem';
    public PROPERTY_QA_VIEW_ITEM: string = 'qaViewItem';

    public PROPERTY_SHOW_PROG_BAR: string = 'showProgressBar';
    public PROPERTY_PROG_BAR_TEXT: string = 'progressBarText';

    public def_ProgressBar_text:string = 'Daten werden geladen .....';

    public error_No_Results:string = 'Ihre suche ergab leider keine Treffer';
    

    public info = {
    };

    constructor(){ }

    addListenerWithName(component: InfoListener,name:string):void{
        //ToDo deactivated code below should be re activated later, after fixing the single Vue Component issue
        //if (!this.listenerNames.includes(name)){
            console.log('Adding listener ' +  name);
            //console.log(component);
            this.listeners.push(component);
            this.listenerNames.push(name);
        //}
    }

    addListener(component: InfoListener):void{
        //console.log('Adding listener ' +  component);
        this.listeners.push(component);

    }

    public getProperty(property:string):any{    
        return this.info[property];
    }
    public setProperty(property:string,value:any){
        this.info[property] = value;
        this.listeners.forEach(currentListener => currentListener.stateChanged(property, value));
    }

    public showNoDataError():void{
        this.showError(this.error_No_Results);
    }
    public showError(message:string):void{
        this.setProperty(this.PROPERTY_ALERT_TEXT,message);
        this.setProperty(this.PROPERTY_SHOW_ALERT,true);

    }
    public clearAlerts():void{
        this.setProperty(this.PROPERTY_CLEAR_ALERTS,true);
    }

    public demo():boolean{
        return true;
        // For this BetFonds Version, we do only have a demo version, the deactivation is due to the fact it will be called via Liberty and the mode is procution from there
        //console.log('process.env.VUE_APP_MODE = ' + process.env.VUE_APP_MODE);
        //return process.env.VUE_APP_MODE == 'demo';
    }
    public fetchURL(urlText:string) :string{
        return this.fetchExternalURL(urlText,'Recherche');
    }

    public fetchExternalURL(urlText:string,relative:string) :string{
        return process.env.NODE_ENV == 'development'?'/' + urlText: '/' + relative + '/' + urlText;
    }


    populateMessage(messages:any){
        console.error(JSON.stringify(messages));
        if (Array.isArray(messages)){
            let messagesText :string = ""; 
            messages.forEach(curMsg =>{
              this.showError(curMsg.Text);    
            });
             
        }else{
            this.showError(messages.Text);
        }
      }
    

    public isTextEmpty(value:string):boolean{
        return (
            (value==undefined) || 
            (value==null) || 
            (value.trim().length==0));


    }

    public isObjectEmpty(value:any):boolean{
        return (
            (value==undefined) || 
            (value==null) ||
            (!value)
        );


    }
    public showProgressBar(message:string):void{
        this.setProperty(this.PROPERTY_PROG_BAR_TEXT,message);
        this.setProperty(this.PROPERTY_SHOW_PROG_BAR,true);
    
    }
    public showDefaultProgressBar():void{
        this.setProperty(this.PROPERTY_PROG_BAR_TEXT,this.def_ProgressBar_text);
        this.setProperty(this.PROPERTY_SHOW_PROG_BAR,true);
    
    }

    public hideProgressBar():void{
        this.setProperty(this.PROPERTY_SHOW_PROG_BAR,false);
    
    }

}


const kfwStore = new KfwStore();

export default kfwStore;