import Vue from 'vue'
import VueRouter from 'vue-router'
import KfwApp from '../module/betFonds/KfwApp.vue';
import BetFondsHistory from '../module/betFonds/panels/BetFondsHistory.vue';
import KfwLogin from '../components/panels/KfwLogin.vue';

Vue.use(VueRouter)

export const routes = [
  {
    path: '/',
    name: 'mainApp',
    component: KfwApp,
    default: true,
    children:[
      
      {
        path: '/history',
        name: 'history',
        component: BetFondsHistory,
      }   
    ],
  },
  {
    path: '/login',
    name: 'login',
    component: KfwLogin,
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
