





































































































































import { Component, Vue, Ref, Prop, Model } from 'vue-property-decorator';
import KfwStore from '../../../KfwStore';
import kfwStore, {InfoListener} from '../../../KfwStore';
import ProkollVorgangUtils from '../../../services/ProkollVorgangUtils';
import FondService from '../service/FondsService';

@Component({
components: {

}, 
})


export default class FondsView extends Vue implements InfoListener {
    @Prop({default:false})
    @Model('click')
    public show:boolean;
    private fondService :FondService;
    public invCategLst : string[]=['Aktienfonds','Anleihen','Immobilien'];
    public fondAnlagFokuLst : string[]=['Precious Metals','Electrum','Future Mobility','Equity','Fixed Income'];
    public branchenFokuLst : string[]=['Aktienfonds','Anleihen','Immobilien'];
    public endAbgLst : string[]=['Ja','Nein'];
    public attInvUmLst : string[]=['Sehr','Mittel','Niedrig'];
    public folgFondLst : string[]=['Ja','Nein'];
    public fondWaehrungLst : string[]=['AED','AUD','EUR','GBP','USD','XOF'];
    public invPhAbgLst : string[]=['Ja','Nein'];



    public invCategItem : String[] = ['Balanced'];
    public endAbgItem : String[] = ['Ja'];
    public folgFondItem : String[] = ['Ja'];
    public fondAnlagFokuItem : string[] = ['Deutschland'];
    public branchenFokuItem : string[] = ['Banken'];
    public attInvUmItem : string[]=['Beispiel: attraktiv'];
    public fondWaehrungItem : string[]=['Euro'];
    public invPhAbgItem : string[]=['Ja'];
    public begAbwRisiko : string ='N/A';


    public utils:ProkollVorgangUtils = new  ProkollVorgangUtils();
    public items:any[]=[];
    public colorsMap:any={
      grün: 'green',
      rot: 'red',
      gelb: 'yellow',
    };
    public segmentHeaders  = [
            {
              text: 'Segment',
              align: 'left',
              sortable: true,
              value: 'SEGMENT',
              class: 'tableHeader',
            },

            {
              text: 'Typ',
              align: 'left',
              sortable: true,
              value: 'TYP',
              class: 'tableHeader',
            },
            {
              text: 'Agnt',
              align: 'left',
              sortable: true,
              value: 'AGENTUR',
              class: 'tableHeader',
            },
            {
              text: 'Wert',
              align: 'left',
              sortable: true,
              value: 'WERT',
              class: 'tableHeader',
            },

  ];
  
  stateChanged(property: string,value: any) {
    if (property==KfwStore.PROPERTY_RATING_VIEW_ITEM){
        let protFields = kfwStore.getProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM);
        
        this.items = this.utils.convertProtFields(protFields);
        console.log("Ratings View : State Changeed ");
        console.log(this.items['SEGMENTE']);


    }

  }

  mounted():void{
    KfwStore.addListenerWithName(this,'RatingsView');
    let protFields = kfwStore.getProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM);
    this.fondService = new FondService();
    this.invCategLst = this.fondService.loadList('Allgemeines/Fonddaten','Investment Categorie');
    this.fondAnlagFokuLst = this.fondService.loadList('Allgemeines/Fonddaten','Regionaler Anlagefokus');
    this.branchenFokuLst = this.fondService.loadList('Allgemeines/Fonddaten','Branchenfokus');
    this.endAbgLst = this.fondService.loadList('Allgemeines/Fonddaten','Endabgerechnet (ja/nein)');
    this.attInvUmLst = this.fondService.loadList('Allgemeines/Fonddaten','Attraktivität des Investitionsumfeldes');
    this.folgFondLst = this.fondService.loadList('Allgemeines/Fonddaten','Folgefond (ja/nein)');
    this.fondWaehrungLst= this.fondService.loadList('Allgemeines/Fonddaten','Fonds-Währung');
    this.invPhAbgLst=this.fondService.loadList('Allgemeines/Fonddaten','Investitionsphase abgeschlossen');

  }

  closeMe(event):void{
    this.$emit('update:show', false);
  }

  getStatusColor(fieldName:string):string{
    let result:string = 'na';
    
    let val = this.items[fieldName];
    
    if (this.colorsMap[val])
      result = this.colorsMap[val];
    console.debug('Getting the status color for field ' + fieldName + ' with the value ' + val  + ' which is mapped to ' + result);
    return result;
  }

}
