
import {FondsHistory_demo,FondView_demo} from './FondsMock'
import KfwStore from '../../../KfwStore';
import axios from 'axios';
import ServiceRequestHandler from '../../../services/ServiceRequestHandler';

import ProkollVorgangUtils from '../../../services/ProkollVorgangUtils';
import * as demoSettings from './DemoSettings.json';



export default class FondsService{
            
    constructor(){}


    public queryRatingHistory(activeItem:any,serviceReqHandler:ServiceRequestHandler):void{
        if (KfwStore.demo()){
            KfwStore.setProperty(KfwStore.PROPERTY_RATINGS_LISTE,FondsHistory_demo);  
            serviceReqHandler.success();
        }else{
            this.queryRatingsHistoryLive(activeItem,serviceReqHandler);
        }

    }

    private queryRatingsHistoryLive(activeItem:any,serviceReqHandler:ServiceRequestHandler):void{
        //console.info(this);
        if (activeItem) {
    
          let vorgangsNumber = KfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
          console.log('Reading the ratings for ' +  activeItem.HGP_ID + " holding the vorgangsnumber " + vorgangsNumber);
    
          let utils :ProkollVorgangUtils  = new ProkollVorgangUtils();
          let flds:any[] = [
            {
              name: 'DisplayVORGANGSOBJEKT',
              content:  activeItem.HGP_ID
            },
            {
              name: 'MANDANT',
              content: '1'
            },
            {
              name: 'BEZART',
              content: '4'
            },
            {
              name: 'GPID',
              content:  activeItem.GPID
            },
            {
              name: 'VORGANGSOBJEKT',
              content:  activeItem.HGP_ID
            },
            {
              name: 'GPNAME',
              content:  activeItem.NAME
            },
    
            ];
    
            KfwStore.showDefaultProgressBar();
            KfwStore.setProperty(KfwStore.PROPERTY_ACTIVE_HGP, activeItem);
            let ratingURL:string = KfwStore.fetchExternalURL('GenericKfwRest','RatingAnzeigeVUE');
            console.debug("Fetching Ratings using the URL : "  + ratingURL);

            //axios.post("/RatingAnzeigeVUE/GenericKfwRest",utils.createNextRequest(vorgangsNumber, activeItem.HGP_ID,flds)).then(res =>{
            axios.post(ratingURL,utils.createNextRequest(vorgangsNumber, activeItem.HGP_ID,flds)).then(res =>{
              if (res.data){
                console.log("Received Response : ");
                console.log(res.data);
                //ToDo the code below should be updated later on with a better error detection handling
                let messages = res.data.PROTOKOLL.BODY.MESSAGES;
                if (messages && messages.MESSAGE){
                  KfwStore.populateMessage(messages.MESSAGE);
                  serviceReqHandler.fail();
                  //this.handleBackAction(true);
                }else{
    
                    let items = utils.getRecords(res.data.PROTOKOLL,"IntRatings");
                    if (items){
                      KfwStore.setProperty(KfwStore.PROPERTY_RATINGS_LISTE,items);
                      serviceReqHandler.success();
                    }else{
                      serviceReqHandler.empty();
                    }
    
              }
    
          }else{
              console.error("No Search results found !!");
              serviceReqHandler.empty();
          }
          }).catch(error =>{
              KfwStore.showError(error);
          }).finally(() => {
              KfwStore.hideProgressBar();
          });
        }else{
          //ToDo deactivated code below should be re activated later, after fixing the single Vue Component issue
          //console.error("No selected item is set, can not send ratings request");
        }
    
    
      }
    
      public queryRatingView(activeItem:any,serviceReqHandler:ServiceRequestHandler):void{
        if (KfwStore.demo()){
            KfwStore.setProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM,FondView_demo);  
            serviceReqHandler.success('Fondsdaten');
        }else{
            this.queryRatingsViewLive(activeItem,serviceReqHandler);
        }

    }

    public queryManagQA(activeItem:any,serviceReqHandler:ServiceRequestHandler):void{
      if (KfwStore.demo()){
          KfwStore.setProperty(KfwStore.PROPERTY_QA_VIEW_ITEM,FondView_demo);  
          serviceReqHandler.success('Managementqualität');
      }else{
          this.queryRatingsViewLive(activeItem,serviceReqHandler);
      }

  }

  public loadList(module:string,listId:string):any{
    if (KfwStore.demo()){
      console.log(demoSettings);
      let moduleEl = demoSettings['default'][module];
      return moduleEl[listId];

    }
    return {};

  }

  public loadModule(module:string):any[]{
    if (KfwStore.demo()){
      return demoSettings['default'][module];

    }
    return  [] ;

  }


  private queryRatingsViewLive(item:any,serviceReqHandler:ServiceRequestHandler):void{
      let utils :ProkollVorgangUtils  = new ProkollVorgangUtils();
      KfwStore.showProgressBar('Rating Übersicht laden ...');
      let activeHGP:any = KfwStore.getProperty(KfwStore.PROPERTY_ACTIVE_HGP);
      let vorgangsNumber = KfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
      console.log(item);
      let flds:any[] = [
              {
              name:    'CLOSE_ON_END',
              content: 'true',
              },
              {
              name:    'GPID',
              content: activeHGP.GPID,
              },
              {
              name:    'GPNAME',
              content: activeHGP.NAME,
              },
              {
              name:    'LAST_APPLICATION',
              content: '/RatingAnzeige',
              },
              {
              name:    'RATING_CREATED',
              content: item.RATING_GUELTVON,
              },
              {
              name:    'RATING_CREATEDBY',
              content: item.RATING_ERFASSER_NAME,
              },
              {
              name:    'RATING_CREATEDBY_ORG',
              content: item.RATING_ERFASSER_ORGEINHEIT,
              },
              {
              name:    'RATING_GUELTBIS',
              content: item.RATING_GUELTBIS,
              },
              {
              name:    'RATING_GUELTVON',
              content: item.RATING_GUELTVON,
              },
              {
              name:    'RATING_ID',
              content: '' + item.RATING_ID,
              },
              {
              name:    'RATING_VERFAHREN',
              content: '' + item.RTG_VERF_AUSPRID,
              },
              {
              name:    'TITLE',
              content: 'Ratingübersicht anzeigen',
              },
              {
              name:    'VORGANGSOBJEKT',
              content: '8295',
              },
          ];

  axios.post("/KRMasken/GenericKfwRest",utils.createRatingSegmentUebersichtRequest(vorgangsNumber,flds)).then(res =>{
    if (res.data){
      console.log("Received Response : ");
      console.log(res.data);
      //ToDo the code below should be updated later on with a better error detection handling
      let messages = res.data.PROTOKOLL.BODY.MESSAGES;
      if (messages && messages.MESSAGE){
        KfwStore.populateMessage(messages.MESSAGE);
        
      }else{
        let fields = res.data.PROTOKOLL.BODY.FIELDS.FIELD;
        if ((fields!=undefined)  && (fields!=null) && (Object.keys(fields).length>0)){
            console.log("ToDo : Received fields, need to populate them in a dialog window");
            KfwStore.setProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM,fields);
            serviceReqHandler.success();
                            
      }else{
        //this.handleRatingsEmtpyResponse();
      }
    }


      }
  }).finally(()=>{
    KfwStore.hideProgressBar();
  });
    }
 

}