





























































































import FondService from '../service/FondsService';
import { Component, Vue, Ref, Prop, Model } from 'vue-property-decorator';
import KfwStore from '../../../KfwStore';
import kfwStore, {InfoListener} from '../../../KfwStore';
import ProkollVorgangUtils from '../../../services/ProkollVorgangUtils';

@Component({
components: {

}, 
})


export default class MangQA extends Vue implements InfoListener {
    @Prop({default:false})
    @Model('click')
    public show:boolean;
    public fondService:FondService = new FondService();
    public groups:any[] = this.fondService.loadModule('Managementqualität');
    public themeDark:boolean = true;
    public themeMiddle: string = 'app_grad_blue_middle';
    public switch2:boolean = false;
    public scores:any[] = [];


    public utils:ProkollVorgangUtils = new  ProkollVorgangUtils();
    public items:any[]=[];
    public colorsMap:any={
      grün: 'green',
      rot: 'red',
      gelb: 'yellow',
    };
    public visibleComments:string[]=[];
    
    public showChoice(value:string):string{
      return this.isSelected(value)?" Ja":" Nein";

    }
    private isSelected(value:string):boolean{
      return (this.scores.indexOf(value)!==-1);
    }

    public isVisible(id:string):boolean{
      return this.visibleComments.indexOf(id)!==-1;
    }

    public showComment(id:string):void{
      const index = this.visibleComments.indexOf(id, 0);
      if (index > -1) {
        this.visibleComments.splice(index, 1);
      }else{
        this.visibleComments.push(id);
      }
    }

    /**
     * 
     * Highlighting the text in case of selection and de-highlighting it otherwise 
     * disclaimer: The below code is dealing specifically with Vuetify Switch control structure and has nothind to do with any generic Programming
     */
    public checkValue():void{
      //${showChoice(frage.text)}`
      let el: any =  event.srcElement;
      if ((el.tagName==='DIV')||(el.tagName==='INPUT'))
        el = el.parentNode.nextSibling;
      
      let curText:string = el.innerHTML;
      /*
      if ((curText.endsWith("Ja"))||(curText.endsWith("Nein")))
         curText = curText.substring(0,curText.length - 4).trim();
         */
      //el.getAttribute("")
      let questId:string = el.getAttribute("for");
      console.log();
      let active:boolean = this.scores.indexOf(curText)!==-1;
      el.className = active?"font-weight-bold":"font-weight-light";
      let hintEl = document.getElementById("hint_" + questId);
      hintEl.innerHTML = active?"<span style=\"opacity:.5;font-weight:normal;\" >Nein</span> - <span style=\"opacity:1;font-weight: bold;\" >Ja</span>":
                                "<span style=\"opacity:1;font-weight:bold;\" >Nein</span> - <span style=\"opacity:.5;font-weight:normal;\" >Ja</span>";
      el.style.cursor = 'pointer';
       
    }


    public segmentHeaders  = [
            {
              text: 'Segment',
              align: 'left',
              sortable: true,
              value: 'SEGMENT',
              class: 'tableHeader',
            },

            {
              text: 'Typ',
              align: 'left',
              sortable: true,
              value: 'TYP',
              class: 'tableHeader',
            },
            {
              text: 'Agnt',
              align: 'left',
              sortable: true,
              value: 'AGENTUR',
              class: 'tableHeader',
            },
            {
              text: 'Wert',
              align: 'left',
              sortable: true,
              value: 'WERT',
              class: 'tableHeader',
            },

  ];

  public getScores():number{
    if (this.scores!=null)
      return this.scores.length;
    return 0;
  }
  
  stateChanged(property: string,value: any) {
    if (property==KfwStore.PROPERTY_RATING_VIEW_ITEM){
        let protFields = kfwStore.getProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM);
        
        this.items = this.utils.convertProtFields(protFields);
        console.log("Ratings View : State Changeed ");
        console.log(this.items['SEGMENTE']);


    }

  }

  mounted():void{
    KfwStore.addListenerWithName(this,'RatingsView');
    let protFields = kfwStore.getProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM);
    console.log("QA Group ========");
    console.log(this.groups);
  }

  closeMe(event):void{
    this.$emit('update:show', false);
  }

  getStatusColor(fieldName:string):string{
    let result:string = 'na';
    
    let val = this.items[fieldName];
    
    if (this.colorsMap[val])
      result = this.colorsMap[val];
    console.debug('Getting the status color for field ' + fieldName + ' with the value ' + val  + ' which is mapped to ' + result);
    return result;
  }

}
