
























































































































import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class AppHeader extends Vue {
    @Prop()
    public theme! :string;
    @Prop()        
    public appTitle! :string;
    @Prop({default:true})
    public themeDark! :boolean;

    public openedMenu!:string;

    menuOpened(title:string){
      console.log("Menu is opened : " + title );
      this.openedMenu=title;
    }
    handleAction(){
      console.log("Handle Action is called " + this.openedMenu);
      if (this.$refs[this.openedMenu]!==undefined){
        let curMenu:any = this.$refs[this.openedMenu][0];
        curMenu.isActive=false;
      }
    }
    
    public menus=[            {
         id: 'Vorgang',
         title: 'Vorgang',
         subMenu: [
          {title: 'Speichern',icon:'mdi-content-save', action: 'save'},
          {title: 'Drucken',icon:'mdi-printer',
            subMenu: [
              {title: 'Bildschirm',action: 'save',icon:'mdi-monitor'},
              {title: 'Datenblatt/Protokoll',icon:'mdi-clipboard-list'}
            ]
          },

          {title: 'Pruefen',action: 'changeTheme',icon:'mdi-file-document-edit-outline'},
          {title: 'Zuruecksetzen',action: 'changeTheme',icon:'mdi-undo-variant'},
          {title: 'Zur Kontrolle freigeben',icon:'mdi-open-in-app'},
          {title: 'Beenden',icon:'mdi-exit-to-app'},
          {title: 'Abbrechen',icon:'mdi-close-circle-outline'},
          
         ]
        },
        {
         id: 'Bearbeiten',
         title: 'Bearbeiten',
         subMenu: [
          {title: 'Ausschneiden', icon:'mdi-content-cut'},
          {title: 'Kopieren', icon:'mdi-content-copy'},
          {title: 'Einfügen', icon:'mdi-content-paste'},
          {title: 'Löschen', icon:'mdi-delete'},
         ]
        },
        {
         id: 'Hilfe',
         title: 'Hilfe',
         subMenu: [
          {title: 'Hilfe',icon:'mdi-help-circle-outline'},
         ]
        },
        ];


}


