
import {RechercheResult_demo} from './RechercheMock'
import KfwStore from '../../../KfwStore';
import axios from 'axios';
import kfwStore from '../../../KfwStore';
import ServiceRequestHandler from '../../../services/ServiceRequestHandler';



export default class RechercheService{
        
    constructor(){}

    public fetchResult( queryText:string ): any[] {
       
        return RechercheResult_demo;
    }

    public doSearch(hgp:string,serviceReqHdl:ServiceRequestHandler):void{

        if (kfwStore.demo()){
            serviceReqHdl.success(RechercheResult_demo);
            
        }else{
            return this.doSearchLive(hgp,serviceReqHdl);
        }
    }
    public doSearchLive(hgp:string,serviceReqHdl:ServiceRequestHandler):void{
        let targetURL : string = KfwStore.fetchURL('KfwRest');
        console.log(' ******************  ENV :: ' + process.env.NODE_ENV + " **** Searching for : " + hgp);
        if (hgp)
            targetURL += '?SC_RECHERCHE_TYPE=HauptGeschaeftspartner&SC_HGPNAME_VALUE=' + hgp;
        console.log("Fetching from URL " + targetURL);
        KfwStore.showDefaultProgressBar();
        axios.get(targetURL).then(res => {
        console.log(res.data);
        if ((res.data.items)&&(res.data.items.length>0)){
            console.log(res.data.items);
            serviceReqHdl.success(res.data.items);
        }else if (res.data){
            serviceReqHdl.success(res.data);
        }else{
            console.error("No Serach results found !!");
            KfwStore.showNoDataError();
            serviceReqHdl.empty();
        }
        }).catch(error =>{
            KfwStore.showError(error);
            serviceReqHdl.fail(error);
        }).finally(() => {
        kfwStore.hideProgressBar();
        });
    }


}