

























































export const Props={
  connectToDB: true,
  connectToServer: true,
  demo: true,
  insideWebContainer:false,
};

import { Component, Vue, Ref, Watch } from 'vue-property-decorator';
import KfwApp from './module/ratingAnzeige/KfwApp.vue';
import KfwLogin from './components/KfwLogin.vue';
import VueRouter from 'vue-router';
import {routes} from './router';
import Axios from 'axios';
import KfwStore from './KfwStore';
import kfwStore, {InfoListener} from './KfwStore';
import AppFooter from './components/panels/AppFooter.vue';
import AppHeader from './components/panels/AppHeader.vue';
import AppMessages from './components/panels/AppMessages.vue';
import Alert from './components/gui/Alert.vue';
import KfwUser from './model/kfwUser';
import ProtokollVorgang from './model/ProtokollVorgang';
import ProtokollVorgangUtils from './services/ProkollVorgangUtils';


@Component({
  components: {
    AppHeader,
    Alert,
    AppFooter,
    AppMessages,
  }, 
})

export default class App extends Vue implements InfoListener {


  public themeMiddle: string = 'app_grad_blue_middle';
  public theme: string = 'app_toolbar_blue';
  public themeDark : boolean = true;
  public appTitle: string = 'Beteiligungsfonds Rating';
  public alertOnError:boolean = false;
  public errorMessage:string = '';
  public errorMessages:string[]=[];
  // Authentication and security  
  public loggedInSession:boolean = false;
  public vorgangsNumber = "";
  public AuthActionLogin:string = '1';
  public AuthActionLogout:string = '2';
  public AuthActionValidate:string = '3';
  public loadingProgress:boolean=false;
  public progressText:string='Daten werden geladen .....';
  public REQUEST_PARAMETER_J_USERNAME:string = 'j_username';
  public REQUEST_PARAMETER_J_MANDANT:string = 'j_mandant';
  public REQUEST_PARAMETER_J_PASSWORD:string = 'j_password';
	public REQUEST_PARAMETER_J_NEW_PASSWORD:string = 'j_password_new';
	
  
  public user_def = {
    id:'J483',
    name: 'Jamil Mohammed',
    mandat:'KFW-ENTW'
  };

  public static vorgangs_def_no:string = '521456321560';
  
  
  public user:KfwUser = new KfwUser();

  constructor(){
    super();
    window.addEventListener("load", this.onWindowLoad);
    KfwStore.addListenerWithName(this,'App.vue');
    KfwStore.setProperty(KfwStore.PROPERTY_THEME_CLASS,this.theme);
    KfwStore.setProperty(KfwStore.PROPERTY_THEME_MIDDLE,this.themeMiddle);
    KfwStore.setProperty(KfwStore.PROPERTY_THEME_DARK,this.themeDark);

  }
  
  private isLoggedIn():boolean{
    return ((localStorage.getItem("user-token")!=null) && (this.loggedInSession));
  }

  onWindowLoad():void{

  }

  @Watch('alertOnError')
  resetMessages(){
    console.debug("Watch alert on error is called, the value was " + this.alertOnError);
    if (!this.alertOnError){
      this.errorMessages=[];
    }
  }

  public mounted():void{
    console.log("Application started  ...  Mode : " + process.env.NODE_ENV );
   if (kfwStore.demo()){
     console.log("Running in Demo mode .....");
    this.user = this.user_def;
    this.openPath('/');
   }else{
    this.handleRouting();  
   }
    
       
  } 

  private handleRouting():void{
    if (Props.insideWebContainer){
      this.openPath('/');
      this.sendVerificationRequest();
      return;
    }

    if (!this.isLoggedIn()){
      this.openPath('/login');
    }else {
      this.fillUserInfo();
      this.openPath('/');
    }
  }

   private fillUserInfo():void{
    if ((this.user.id==null) && (localStorage.getItem('user-token'))){
      let jwtData = JSON.parse(atob(localStorage.getItem('user-token').split('.')[1]));
      this.user.id = jwtData.userId;
      this.user.name = jwtData.userName;
      this.user.mandat = jwtData.mandat;
    }
  }

  // Activating the vorgang via flat get request
  public static handleVorgangActivationAsGet(){
    let vorgangsNumber = kfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
    let verificationURL = KfwStore.fetchExternalURL('Vor','RatingAnzeigeVUE');
    let activationURLInfo = '?format=json&ACTION=UeberHauptordnungsbegriff&ACTION_INT=45&EINGEBEN=true&GRUPPE_AUSFUEHREN=true&MANDANT=1&ME_BEENDEN=false&POSITIONIEREN=false&SCHRITT=VorgangBearbeiten&TERMINIEREN=false&VORGANGSTYP=2389&VORHANDEN=false&WEB_APP_OBJECT=RatingAnzeige@WEB_APP_PATH=RatingAnzeigeVUE';
    verificationURL += activationURLInfo; 
    Axios.get(verificationURL).then(res => {
    if (res.data){
        console.log('Received Vorgangs Number from the server : ' + res.data.PROTOKOLL.VORGANG.VORGANGSNUMMER);
        let vorgangsNumber:string = res.data.PROTOKOLL.VORGANG.VORGANGSNUMMER;
        kfwStore.setProperty(KfwStore.PROPERTY_VORGANGS_NUMBER,vorgangsNumber);
        KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_NEXT,true);
        
    }else{
        console.error("No Serach results found !!");
    }
    }).catch(error =>{
        KfwStore.showError(error);
    }).finally(() => {

    });

  }


  // Activating the vorgang via json post request (semi HAL)
  public static handleVorgangActivation():void{
    if (kfwStore.demo()){
      kfwStore.setProperty(KfwStore.PROPERTY_VORGANGS_NUMBER,this.vorgangs_def_no);
      KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_NEXT,true);

    }else{
      let vorgangsNumber = kfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
      let utils :ProtokollVorgangUtils  = new ProtokollVorgangUtils();
      KfwStore.showProgressBar('Neu Vorgang beantragen ... ');
      let ratingURL:string = KfwStore.fetchExternalURL('GenericKfwRest','RatingAnzeigeVUE');
      console.debug("Fetching Ratings using the URL : "  + ratingURL);

      Axios.post(ratingURL,utils.createInitRequest()).then(res =>{
        if (res.data){
            console.log('Received Vorgangs Number from the server : ' + res.data.PROTOKOLL.VORGANG.VORGANGSNUMMER);
            vorgangsNumber = res.data.PROTOKOLL.VORGANG.VORGANGSNUMMER;
            kfwStore.setProperty(KfwStore.PROPERTY_VORGANGS_NUMBER,vorgangsNumber);
            KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_NEXT,true);
          }else{
            console.error("No Serach results found !!");
          }
        }).catch(error =>{
            KfwStore.showError(error);
        }).finally(() => {
          KfwStore.hideProgressBar();

        });
      }
  }

  openPath(path:string):void{ 
    if(this.$router.currentRoute.path!=path)
        this.$router.replace(path);

  }

  getErrorMessagesAsText():string{
    let result :string = '';
    this.errorMessages.forEach(curMsg =>{result += curMsg + ' .... '});
    return result;
  }


  // Sending test request
  doEditDocument():void{
    kfwStore.clearAlerts();
  }

  doChangeTheme():void{
    this.theme = this.themeDark?'app_toolbar_gray':'app_toolbar_blue';
    this.themeMiddle = this.themeDark?'app_grad_gray_middle':'app_grad_blue_middle';
    this.themeDark = !this.themeDark;
    KfwStore.setProperty(KfwStore.PROPERTY_THEME_DARK,this.themeDark);
    KfwStore.setProperty(KfwStore.PROPERTY_THEME_CLASS,this.theme);
    KfwStore.setProperty(KfwStore.PROPERTY_THEME_MIDDLE,this.themeMiddle);

  }

  handleLogin(e):void{
    KfwStore.showProgressBar('Anmeldeanforderung senden ...');
    /* Old style logging via appending the reuest parameters
    const params = new URLSearchParams();
    params.append(this.REQUEST_PARAMETER_J_USERNAME,e.user);
    params.append(this.REQUEST_PARAMETER_J_PASSWORD,e.password);
    params.append('action',this.AuthActionLogin);
    */
    let parms:any = {};
    parms[this.REQUEST_PARAMETER_J_PASSWORD] = e.pwd;
    parms[this.REQUEST_PARAMETER_J_USERNAME] = e.user;
    parms['action'] = this.AuthActionLogin;
    
    let authURL:string = KfwStore.fetchExternalURL('Authenticate','RatingAnzeigeVUE');
    console.log('Logging a user ' + JSON.stringify(parms) + ' over the url ' + authURL );
    Axios.post<any>(authURL,parms).then(res =>{
      this.processIncomingToken(res);
    }).catch(error =>{
      console.log("Login Failed :  " + error);
      this.handleLoginFailure();
    }).finally(() => {
      KfwStore.hideProgressBar();
    });
  }

  processIncomingToken(res: any):void{
    if (res.data.token){
      console.log('Successfully logged in and got the token : ' + res.data.token);
      localStorage.setItem('user-token',res.data.token);
      let jwtData = JSON.parse(atob(res.data.token.split('.')[1]));
      this.user.id = jwtData.userId;
      this.user.name = jwtData.userName;
      this.user.mandat = jwtData.mandat;
      this.loggedInSession = true;
      Axios.defaults.headers.common['Authorization'] = 'Bearer ' + res.data.token;
      console.log(jwtData);
      this.openPath('/');
    }else{
      console.log('Response ::: ' +  res.data);
      this.handleLoginFailure();
    }

  }
  
  sendVerificationRequest():void{
    const params = new URLSearchParams();
    KfwStore.showProgressBar('Senden einer Bestätigungsanfrage ...'); 
    params.append('action',this.AuthActionValidate);
    //{username: e.user,password:e.pwd,action:this.AuthActionLogin}
    Axios.post<any>("http://localhost:9080/RatingAnzeigeVUE/Authenticate",params).then(res =>{
          this.processIncomingToken(res);
      }).catch(error =>{
        console.log("Verification Failed :  " + error);
        this.openPath('/login');
        }).finally(() => {
          KfwStore.hideProgressBar();
      });

  }

  handleLoginFailure():void{
    KfwStore.showError('Login Failed !');
    KfwStore.setProperty(KfwStore.PROPERTY_CLEAR_LOGIN,true);
  }
    
  public doLogOut():void{
    // Nothing to do if we are inside the container
    if (Props.insideWebContainer) return;
    const params = new URLSearchParams();
    params.append(this.REQUEST_PARAMETER_J_USERNAME,this.user.id);
    params.append(this.REQUEST_PARAMETER_J_PASSWORD,'');
    params.append('action',this.AuthActionLogout);

    Axios.post<any>("http://localhost:9080/RatingAnzeigeVUE/Authenticate",params).then(res =>{
      localStorage.removeItem('user-token');
      delete Axios.defaults.headers.common['Authorization'];
      this.resetUser();
      this.handleRouting();
      this.loggedInSession = false;

    }).catch(error =>{
      console.log(" Logout Error : " + error);
      
    });

  }

  goNext():void{
    KfwStore.setProperty(KfwStore.PROPERTY_ACTION_NEXT,true);

  }
  goBack():void{
    this.errorMessages=[];
    KfwStore.setProperty(KfwStore.PROPERTY_ACTION_BACK,true);


  }


    public stateChanged(property:string,newValue:any):void{
      if (property==KfwStore.PROPERTY_SHOW_ALERT)
        this.alertOnError = newValue;
      if (property==KfwStore.PROPERTY_ALERT_TEXT)
        this.errorMessages.push(newValue);
      if ((property==KfwStore.PROPERTY_VORGANGS_NUMBER)||(property==KfwStore.PROPERTY_CLEAR_ALERTS))
        this.errorMessages =[];
      if (property==KfwStore.PROPERTY_SHOW_PROG_BAR)
        this.loadingProgress = newValue;
      if (property==KfwStore.PROPERTY_PROG_BAR_TEXT)
        this.progressText= newValue;
  }

  private resetUser():void{
      this.user.id = null;
      this.user.name = null;
      this.user.mandat = null;

  }
}

