


















































    import { Component, Vue, Prop, Model } from 'vue-property-decorator';
    import RechercheResult from './RechercheResult.vue';
    import RechercheService from '../../module/betFonds/service/RechercheService';
    import KfwStore from '../../KfwStore';
    import axios from 'axios';
    import ServiceRequestHandler from '../../services/ServiceRequestHandler';
import kfwStore from '../../KfwStore';
    @Component(
        {
        components:{
            RechercheResult,

        }
    })
    export default class TextRechercheFeld extends Vue implements ServiceRequestHandler {
        $refs!:{
            rechercheList:RechercheResult;
        }

        @Prop()
        @Model('input')
        public value:string;
        @Prop()
        public label! :string;
        @Prop()
        public reference! :string;
        @Prop()
        public disabled! :boolean;
        @Prop()
        public headers:any[];


        public rechercheResult: any={};

        public loadingProgress:boolean = false;
        public showRechercheResult:boolean = false;
        private rechercheService:RechercheService = new RechercheService();

        doSearch():void{
            this.rechercheService.doSearch(this.value,this);
        }

        success(records:any){
            this.rechercheResult = records;
            this.showRechercheResult = true;
            if (this.$refs.rechercheList)
                this.$refs.rechercheList.sendToJSP();

        }
        fail(){}
        empty(){}
        
        handleItemSelection(item:any):void{
            this.$emit('itemSelected',item);
            this.showRechercheResult = false;
        }
        handleInput(event:any):void{
            this.$emit('input',event);
        }

        handleChange(event:any):void{
            this.$emit('change',event);
        }
}
