
import ProtokollVorgang from '../model/ProtokollVorgang';

export default class ProtokollVorgangUtils{


    public createEmptyRequest():ProtokollVorgang{
        let req:ProtokollVorgang = new ProtokollVorgang();
        let vorgang = req.PROTOKOLL.VORGANG;
        vorgang.ACTION='';
        vorgang.ACTION_INT='';
        vorgang.AKTUELLE_MASKE='';
        vorgang.EINGEBEN='';
        vorgang.GRUPPE_AUSFUEHREN='';
        vorgang.MANDANT='';
        vorgang.ME_BEENDEN='';
        vorgang.NAECHSTE_MASKE='';
        vorgang.POSITIONIEREN='';
        vorgang.SCHRITT='';
        vorgang.TERMINIEREN='';
        vorgang.VORGANGSNUMMER='';
        vorgang.VORGANGSOBJEKT='';
        vorgang.VORGANGSOBJEKTTYP='';
        vorgang.VORGANGSTYP='';
        vorgang.VORG_ACTION='';
        vorgang.VORG_SESSION_ID='';
        vorgang.VORG_WEB_APP_OBJECT='';
        vorgang.VORG_WEB_APP_PATH='';
        vorgang.VORHANDEN='';
        vorgang.WEB_APP_OBJECT='';
        vorgang.WEB_APP_PATH='';
        vorgang.WEB_APP_PATH='';
        return req;
    }
    
    private addDefaults(req:ProtokollVorgang):void{
        let vorgang = req.PROTOKOLL.VORGANG;
        vorgang.MANDANT='1';
        vorgang.WEB_APP_OBJECT='RatingAnzeige';
        vorgang.WEB_APP_PATH='RatingAnzeigeVUE';
    }

    private appendFields(req: ProtokollVorgang,flds:any):void{
        let vorgFlds = req.PROTOKOLL.BODY.FIELDS;
        let vorgFldArray :any[] = [];
        flds.forEach(element => {
            vorgFldArray.push({Name:element.name,content: element.content});
        });
        vorgFlds.FIELD = vorgFldArray;

    }


    public createInitRequest():ProtokollVorgang{
        let req:ProtokollVorgang = new ProtokollVorgang();
        let vorgang = req.PROTOKOLL.VORGANG;
        this.addDefaults(req);
        vorgang.ACTION='UeberHauptordnungsbegriff';
        vorgang.ACTION_INT='45';
        vorgang.EINGEBEN='true';
        vorgang.GRUPPE_AUSFUEHREN='true';
        vorgang.ME_BEENDEN='false';
        vorgang.NAECHSTE_MASKE='';
        vorgang.POSITIONIEREN='false';
        vorgang.SCHRITT='VorgangBearbeiten';
        vorgang.TERMINIEREN='false';
        vorgang.VORGANGSTYP='2389';
        vorgang.VORHANDEN='false';
        return req;
    }


    public createNextRequest(vorgangsNumber:string,vorgObj:string,flds:any):ProtokollVorgang{
        let req:ProtokollVorgang = new ProtokollVorgang();
        let vorgang = req.PROTOKOLL.VORGANG;
        this.addDefaults(req);
        vorgang.ACTION='Weiter';
        vorgang.ACTION_INT='0';
        vorgang.AKTUELLE_MASKE='HOBEingabeGP';
        vorgang.EINGEBEN='true';
        vorgang.GRUPPE_AUSFUEHREN='false';
        vorgang.ME_BEENDEN='false';
        vorgang.POSITIONIEREN='true';
        vorgang.TERMINIEREN='false';
        vorgang.VORGANGSNUMMER= ''+ vorgangsNumber;
        vorgang.VORGANGSOBJEKT=vorgObj;
        vorgang.VORGANGSTYP='0';
        vorgang.VORHANDEN='true';
        this.appendFields(req,flds);
        return req;


    }

    public createRatingSegmentUebersichtRequest(vorgangsNumber:string,flds:any):ProtokollVorgang{
        let req:ProtokollVorgang = new ProtokollVorgang();
        let vorgang = req.PROTOKOLL.VORGANG;
        vorgang.ACTION='RatingSegmentUebersicht';
        vorgang.ACTION_INT='61';
        vorgang.EINGEBEN='true';
        vorgang.GRUPPE_AUSFUEHREN='true';
        vorgang.MANDANT='1';
        vorgang.ME_BEENDEN='false';
        vorgang.POSITIONIEREN='false';
        vorgang.REMOTESCHRITT='true';
        vorgang.SCHRITT='VorgangAnzeigen';
        vorgang.TERMINIEREN='false';
        vorgang.VORGANGSOBJEKT='8295';
        vorgang.VORGANGSOBJEKTTYP='924';
        vorgang.VORGANGSTYP='2389';
        vorgang.VORG_ACTION='MitObjekt';
        vorgang.VORG_SESSION_ID=''+ vorgangsNumber;
        vorgang.VORG_WEB_APP_OBJECT='RatingAnzeige';
        vorgang.VORG_WEB_APP_PATH='RatingAnzeigeRND';
        vorgang.VORHANDEN='false';
        vorgang.WEB_APP_OBJECT='KRMasken';
        vorgang.WEB_APP_PATH='KRMasken';
        vorgang.handler='de.kfw.vg.steuern.eventhandler.message.E1355MessagesToProtokollHandler';
        this.appendFields(req,flds);
        
        return req;
    }

    public convertProtFields(fields:any[]):any{
        let items :any = {};
        if ((fields!=undefined)  && (fields!=null) && (Object.keys(fields).length>0)){
            fields.forEach(servItem =>{
                let newItem:any = {};
                if (servItem.content){
                    items[servItem.Name] = servItem.content;
                }else if (servItem.RECORDS){
                    items[servItem.Name] = this.convertProtRecords(fields,servItem.Name);
                }
                
            });
        }

        return items;
    }


    public getFields(prot:any):any{
        let fields = prot.BODY.FIELDS.FIELD;
        return this.convertProtFields(fields);
    }

    public getRecords(prot:any,fieldName:string):any{
        let fields = prot.BODY.FIELDS.FIELD;
        return this.convertProtRecords(fields,fieldName);
    }

    public convertProtRecords(fields:any[],fieldName:string):any[]{
        let items :any[] = [];
        if ((fields!=undefined)  && (fields!=null) && (Object.keys(fields).length>0)){
            // In case of a single record, the sent item is Json and not array
            let targetField = fields.find(curFld => curFld.Name==fieldName);
            if (targetField){
                let mainRecord = targetField.RECORDS.RECORD;
                if (Array.isArray(mainRecord)){
                    targetField.RECORDS.RECORD.forEach(curRecord =>{
                        items.push(this.createSingleRecord(curRecord.ATTRIBUTE))
                    });
                }else{
                    items.push(this.createSingleRecord(mainRecord.ATTRIBUTE))
                }
            }
        }
        return items;
    }

    private createSingleRecord(attrs:any[]):any{
        let newItem:any = {};
        attrs.forEach(curItem =>{
            newItem[curItem.id] = curItem.content;
        });
        return newItem;
    }


}