








































































    import { Component, Vue, Ref } from 'vue-property-decorator';
    import {FondsTableHeaders} from '../model/FondSettings';
    import KfwStore from '../../../KfwStore';
    import kfwStore, {InfoListener} from '../../../KfwStore';
    import axios from 'axios';
    import ProkollVorgangUtils from '../../../services/ProkollVorgangUtils';
    import FondsView from './FondsView.vue';
    import MangQA from './MangQA.vue';
    import ServiceRequestHandler from '../../../services/ServiceRequestHandler';
    import FondsService from '../../betFonds/service/FondsService';

    @Component({
    components: {
        FondsView,
        MangQA
    }, 
    })

    export default class BetFondsHistory extends Vue implements InfoListener,ServiceRequestHandler {
        public headers = FondsTableHeaders;
        public fondsResult: any[]=[];
        public resultSearch:string='';
        public themeMiddle: string = 'app_grad_blue_middle';
        public theme: string = 'app_toolbar_blue';
        public themeDark:boolean = true;
        public showView:boolean = false;
        public showQA:boolean = false;
        private fondsService:FondsService = new FondsService();

        public ddmItems :any[]=[
            { title: 'Fondsdaten'        ,icon: 'mdi-view-list',flag:'MENUE_UEBERSICHT_ACTIVE' },
            { title: 'Managementqualität' ,icon: 'mdi-view-list',flag:'MENUE_DETAIL_ACTIVE' },
        ];
        handleDDMenuClick(menuItem:string,item:any):void{
            console.log("DD Menu is clicked, menu Item is : " + menuItem + " being clicked from the item " + item.RATING_ID + " , active :" + item.MENUE_DETAIL_ACTIVE);
            if (menuItem=='Fondsdaten'){
                this.fondsService.queryRatingView(item,this);
            }else if (menuItem=='Managementqualität'){
                this.fondsService.queryManagQA(item,this);
            }
            

        }
        mounted():void{
            console.log("RatingHistory Mounteed is called .....");
            KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_NEXT,false);
            KfwStore.setProperty(KfwStore.PROPERTY_ACTIVATE_BACK,true);
            KfwStore.addListenerWithName(this,'BetFondsHistory.vue');
            this.fondsResult = KfwStore.getProperty(KfwStore.PROPERTY_RATINGS_LISTE); 
            this.theme = KfwStore.getProperty(KfwStore.PROPERTY_THEME_CLASS);
            this.themeMiddle = KfwStore.getProperty(KfwStore.PROPERTY_THEME_MIDDLE);
            this.themeDark = KfwStore.getProperty(KfwStore.PROPERTY_THEME_DARK); 


        }

           
        success(target:any){
            if (target==='Fondsdaten')
                this.showView = true;
            if (target==='Managementqualität')
                this.showQA = true;

        }

        fail(){ }

        empty(){ }


        public stateChanged(property:string,newValue:any):void{
            if (property==KfwStore.PROPERTY_THEME_DARK)
                this.themeDark = newValue;
            if (property==KfwStore.PROPERTY_THEME_CLASS)
                this.theme = newValue;
            if (property==KfwStore.PROPERTY_THEME_MIDDLE)
                this.themeMiddle = newValue;
            if (property==KfwStore.PROPERTY_RATINGS_LISTE)
                this.fondsResult = newValue;

        }

        public sendUebersichtRequest(item:any):void{
            let utils :ProkollVorgangUtils  = new ProkollVorgangUtils();
            let activeHGP:any = KfwStore.getProperty(KfwStore.PROPERTY_ACTIVE_HGP);
            let vorgangsNumber = KfwStore.getProperty(KfwStore.PROPERTY_VORGANGS_NUMBER);
            console.log(item);
            let flds:any[] = [
                    {
                    name:    'CLOSE_ON_END',
                    content: 'true',
                    },
                    {
                    name:    'GPID',
                    content: activeHGP.GPID,
                    },
                    {
                    name:    'GPNAME',
                    content: activeHGP.NAME,
                    },
                    {
                    name:    'LAST_APPLICATION',
                    content: '/RatingAnzeige',
                    },
                    {
                    name:    'RATING_CREATED',
                    content: item.RATING_GUELTVON,
                    },
                    {
                    name:    'RATING_CREATEDBY',
                    content: item.RATING_ERFASSER_NAME,
                    },
                    {
                    name:    'RATING_CREATEDBY_ORG',
                    content: item.RATING_ERFASSER_ORGEINHEIT,
                    },
                    {
                    name:    'RATING_GUELTBIS',
                    content: item.RATING_GUELTBIS,
                    },
                    {
                    name:    'RATING_GUELTVON',
                    content: item.RATING_GUELTVON,
                    },
                    {
                    name:    'RATING_ID',
                    content: '' + item.RATING_ID,
                    },
                    {
                    name:    'RATING_VERFAHREN',
                    content: '' + item.RTG_VERF_AUSPRID,
                    },
                    {
                    name:    'TITLE',
                    content: 'Ratingübersicht anzeigen',
                    },
                    {
                    name:    'VORGANGSOBJEKT',
                    content: '8295',
                    },
                ];
        axios.post("http://localhost:9080/KRMasken/GenericKfwRest",utils.createRatingSegmentUebersichtRequest(vorgangsNumber,flds)).then(res =>{
          if (res.data){
            console.log("Received Response : ");
            console.log(res.data);
            //ToDo the code below should be updated later on with a better error detection handling
            let messages = res.data.PROTOKOLL.BODY.MESSAGES;
            if (messages && messages.MESSAGE){
              KfwStore.populateMessage(messages.MESSAGE);
              
            }else{
              let fields = res.data.PROTOKOLL.BODY.FIELDS.FIELD;
              if ((fields!=undefined)  && (fields!=null) && (Object.keys(fields).length>0)){
                  console.log("ToDo : Received fields, need to populate them in a dialog window");
                  kfwStore.setProperty(KfwStore.PROPERTY_RATING_VIEW_ITEM,fields);
                  this.showView = true;
                                  
            }else{
              //this.handleRatingsEmtpyResponse();
            }
          }


            }
        });
    }

}
