




























import { Component, Vue, Prop } from 'vue-property-decorator';

import KfwUser from '../../model/kfwUser';
import KfwStore from '../../KfwStore';
import {InfoListener} from '../../KfwStore';

@Component
export default class AppMessages extends Vue implements InfoListener{
  panelVisible:boolean = false;  
  consoleEl!:HTMLElement;
  headerEl!:HTMLElement;
  public messagesText:string[]= [];
  
  public stateChanged(property: string,value: any):void {
    if (property==KfwStore.PROPERTY_ALERT_TEXT)
        this.messagesText.push(value);
    // Logging property change event
    //this.messagesText.push('Property : ' + property + ' , Value : ' + value );
  }

  

  mounted():void{
    this.consoleEl = document.getElementById("msgConsole");
    this.headerEl  = document.getElementById("header");
    this.headerEl.className ='header';
    this.consoleEl.className = 'msgConsole';
    KfwStore.addListenerWithName(this,'AppMessages.vue');
    // Testing the messages
    //this.messagesText.push("Testing Error message");
    //this.messagesText.push("Another test for the Error message");
  }

  public showPanel():void{
      this.consoleEl.style.zIndex='10';
      this.consoleEl.className = this.panelVisible?'msgConsole':'msgConsole consoleActive';
      this.headerEl.className = this.panelVisible?'header':'header headerActive';
      this.panelVisible = !this.panelVisible;
  }

  public clearMessages():void{
      this.messagesText=[];
  }
  public messageSize():string{return String(this.messagesText.length);}
}
